@import "../../globalSASS/global.scss";
.radio {
	display: flex;
	justify-content: space-between;

	width: 11.8em;

	&__item {
		position: relative;
	}

	&__input {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		opacity: 0;
		z-index: -1;
	}

	&__button {
		font-size: 19 / 18 + em;
		background-color: #f9f9f9;
		border: 1px solid #F2F2F2;
		border-radius: 5px;
		padding: 0.5em;

		cursor: pointer;

		&:hover {
			border: 1px solid #9e9e9e;
		}

	}

	&__current {
		background-color: #f1bf3e;
	}
}
