@import "../../globalSASS/global.scss";
.bar {
	position: fixed;
	top:0;
	width: 100vw;
	padding-top: 1.5em;
    
    background-color: rgba($color: #000000, $alpha: 0.2);

	z-index: 2;

	&__wrap {

		display: flex;
		justify-content: space-between;
		align-items: flex-start;

		width: 66.5%;
		height: 3.5em;

		margin-left: auto;
		margin-right: auto;
	}

	&__side-l {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 41%;
	}

	&__side-r {
		position: relative;

		display: flex;
		align-items: center;

		height: 37 / 18 + em;
	}

	&__phone {
		font-size: 22 / 18 + em;
		font-weight: 300;
		text-decoration: none;
		color: #fff;

		&:hover {
			filter: drop-shadow( 0 0 5px #000);
        }
        &:active {
			color: $colorOrange;
		}
	}

	&__call {
		position: absolute;
		top: 80%;
		left: 10%;

		color: #fff;
		text-decoration: none;
		font-weight: 300;

		border-bottom: 1.5px solid $colorOrange;

		&:hover {
			filter: drop-shadow( 0 0 5px #000);
			border-bottom: 1.5px solid #fff;
        }
        &:active {
			color: $colorOrange;
			border-bottom: 1.5px solid $colorOrange;
		}
	}
}
