@import "../../globalSASS/global.scss";
.locator {
    position: absolute;
    top: 15.5%;
    right: 19.2%;
    transform: translate(50%, -50%);

    width: 51vw;
    height: 51vw;

    &__wrap {
        position: relative;
        width: 100%;
        height: 100%;
    }

    &__radius {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);

        border: 1px solid #D6D6D6;
        border-radius: 50%;
        
        

        @mixin circle($size) {
            height: $size;
            width: $size;
        }

        &_s {
           @include circle(45.5%);
           top: 45.5%;
           left: 51.5%;
        }
        
        &_m {
            @include circle(56%);
            top: 45.5%;
            left: 51.5%;
            opacity: 0.38;
        }

        &_l {
            @include circle(77%);
            top: 48%;
            left: 51.5%;
        }

        &_xl {
            @include circle(86%);
            opacity: 0.36;
        }

        &_xxl {
            @include circle(100%);
        }
    }

    &__item {
        position: absolute;

        display: flex;
        align-items: center;

        &_1 {
            top: 53.2%;
            left: 5.6%;
        }

        &_2 {
            top: 63.5%;
            left: 30%;
        }

        &_3 {
            top: 79.5%;
            left: 19.3%;
        }
    }

    &__circle {
        display: flex;
        align-items: center;
        justify-content: center;

        width: 30px;
        height: 30px;

        border: 1px solid rgba($color: #d6d6d6, $alpha: 0.4);
        border-radius: 50%;
    }

    &__point {
        width: 20px;
        height: 20px;

        border-radius: 50%;
        background-color: #F1C65A;
        box-shadow: 0px 0px 7px #F1C65A;
    }

    &__text {
        padding-left: 8px;
        color: #fff;
        font-weight: 300;
    }
}