@import "../../globalSASS/global.scss";
.accordion {
    width: 100%;

    &__item {
        position: relative;

        background-color: #ffffff;
        box-shadow: 0px 0px 20px rgba(0, 0, 0, 0.17);
        border-radius: 10px;
        margin-bottom: 2.8%;
        
        color: #313131;
    }

    &__question {
        margin: 0;
        font-size: 25 / 18 + em;
        font-weight: normal;
        padding-left: 1.2em;
        padding-right: 7em;
        padding-top: 1.2em;
        padding-bottom: 1.2em;
    }

    &__answer {
        margin: 0;
        display: none;
        padding-right: 10%;
        padding-left: 1.4em;
        padding-bottom: 1.4em;
        margin-top: -0.5em;

        font-size: 20 / 18 + em;
        font-weight: 300;
    }

    &__svg {
        position: absolute;
        top: 50%;
        right: 1.5%;

        transform: translate(-50%, -50%) rotate(0deg);

        width: 48px;
        height: 48px;

        cursor: pointer;

        &:hover {
            --primary-color: #fdb500;
        }
       
    }
}