@import "../../globalSASS/global.scss";
.puls {
	position: absolute;
	top: 51%;
	left: 50%;

    transform: translate(-50%, -50%);
    
    $w: 37.5;
    $h: 38.5;
    $dev: 1.08;

	width: $w + vw;
    height: $h + vw;

    animation: pulse 3s infinite;
    @keyframes pulse {
        0% {
            width: $w / $dev + vw;
            height: $h / $dev + vw;
        }

        60% {
            width: $w + vw;
            height: $h + vw;
        }

        100% {
            width: $w / $dev + vw;
            height: $h / $dev + vw;
        }
    }

	&__wrap {
		position: relative;
		width: 100%;
        height: 100%;
	}

	&__circle {
		position: absolute;
		top: 50%;
		left: 50%;

		transform: translate(-50%, -50%);

		border: 1px solid #FAE8AE;
        border-radius: 50%;
        
        &_s {
            width: 54%;
            height: 53%;
            opacity: 0.4;
        }

		&_m {
			width: 78%;
            height: 78%;
            opacity: 0.7;
		}

		&_l {
			width: 100%;
            height: 100%;
            opacity: 0.55;
		}
	}
}
