@import "../../globalSASS/global.scss";
.heading {
    display: flex;
    flex-direction: column;

    &_pos {
        position: absolute;  
        left: 0;
        top: 27%;
    }

    &__svg {
        width: 34em;
    }

    &__text {
        font-size: 40 / 18 + em;
        font-weight: 300;
        line-height: 1em;
    }
}