@import "../../globalSASS/global.scss";
.menu {
    display: flex;
    justify-content: space-between;
    min-width: 300px;

    &__link {
        text-decoration: none;
        color: #fff;
        font-weight: 300;
        &:hover {
			filter: drop-shadow( 0 0 5px #000);
        }
        &:active {
			color: $colorOrange;
		}
    }
}