@import "../../globalSASS/global.scss";
.select {
    position: relative;
    width: 11.8em;
    height: 2em;
    background-color: #F9F9F9;
    border: 1px solid #F2F2F2;
    border-radius: 5px;
    
    cursor: pointer;
    overflow: hidden;


    &:hover {
        border: 1px solid #9e9e9e;
    }

    &__type {
        position: absolute;
        top: 0;
        left: 0;
        width: 120%;
        height: 100%;
        font-size: 19 / 18 + em;
        padding-left: 0.5em;
        background: transparent;
        outline: none;
        border: none;
        z-index: 2;
        cursor: pointer;

        
    }
}