@import "../../globalSASS/global.scss";
.button {

    padding: 1em 1.8em;

    font-size: 20 / 18 + em;
    font-weight: 300;
    color: #313131;

    border: none;
    border-radius: 100em;
    background: linear-gradient(180deg, #FBEAB4 0%, #EFBF49 100%);
    box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.28);

    cursor: pointer;
    outline: none;
    transition: 0.3s;

    &:hover {
        transform: scale(1.05);
        transition: 0.5s;
    }
    &:active {
        box-shadow: 0px 6px 20px rgba(255, 255, 255, 0.5);
        transition: 0s;
    }

    &_header {
        position: absolute;
        left: 0;
        top: 65%;

        &:hover {
            transform: scale(1.05);
            transition: 0.5s;
        }
        &:active {
            box-shadow: 0px 6px 20px rgba(255, 255, 255, 0.5);
            transition: 0s;
        }
    
    }

    &_card {
        white-space: nowrap;
        padding: 0.9em 2.8em;
        margin-left: 50%;
        margin-top: 11.8%;
        margin-bottom: 10.5%;
        transform: translate(-50%, 0);
        font-size: 21 / 18 + em;

        &:hover {
            transform: translate(-50%, 0) scale(1.05);
            transition: 0.5s;
        }
        &:active {
            box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.5);
            transition: 0s;
        }
    }

    &_calculate {
        margin-top: 9%;
        margin-left: 16%;

        &:active {
            box-shadow: 0px 6px 20px rgba(0, 0, 0, 0.5);
            transition: 0s;
        }

        @media screen and (max-width: 1650px) {
            margin-top: 5%;
        }
    }
}