@import "../../globalSASS/global.scss";
.cards {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    width: 69.7vw;
    margin-left: auto;
    margin-right: auto;

    &_pos {
        margin-top: 3.7em;
        padding-bottom: 2%;
    }

    &__arrow {
        position: absolute;
        top: 42.5%;

        transform: translate(-50%, -50%);
        height: 90px;
        width: 30px;
        transition: 0.2s;
        cursor: pointer;

        &:hover {
            filter: drop-shadow(0 0 5px #000);
        }

        &:active {
            --primary-color: rgb(255, 187, 0);
            filter: drop-shadow(0 0 5px #fff);
        }
        
        &_prev {
            left: -4.5%;
        }

        &_next {
            right: -6.5%;
        }
    }
    
}