@import "../../globalSASS/global.scss";
.carousel {
	position: relative;
	width: 100%;
    overflow: hidden;
    
    &__wrap {
        display: flex;
        transition: transform .5s;
		position: relative;
        z-index: 1;
        
        
    }

	&__item {
		flex: 0 0 100%;
		position: relative;
		display: flex;
		align-items: flex-start;

		width: 100%;
        padding: 30px 0;
        
        overflow: hidden;
	}
}
