@import "../../globalSASS/global.scss";
.logo {
	display: flex;
	align-items: center;
	width: 8em;
	height: 37 / 18 + em;
	color: $colorOrange;
	text-decoration: none;

	&:hover {
		filter: drop-shadow(0 0 5px #000);
	}

	&:active {
		--primary-color: #fff;
		color: #fff;
	}



	&__svg {
		height: 100%;
		width: 37 / 18 + em;
	}

	&__text {
		font-size: 19 / 18 + em;
		text-transform: uppercase;
		font-weight: bold;
		line-height: 100%;
		padding-left: 0.5em;
	}
}
