@import "../../globalSASS/global.scss";
.input {
    position: relative;
    height: 2em;
    width: 92px;
    background-color: #f9f9f9;

    &__area {
        width: 40%;
        height: 100%;
        padding-left: 1em;
       
        font-size: 19 / 18 + em;

        border: none;
        outline: none;
        background-color: transparent;
    }

    &__vertical {
        position: absolute;
        top: 50%;
        left: 10%;
        color: #c0c0c0;

        transform: translate(-50%, -50%);
    }

    &__unit {
        position: absolute;

        top: 48%;
        right: 0%;

        transform: translate(-50%, -50%);

        font-size: 17 / 18 + em;
    }
}