@import "../../globalSASS/global.scss";
.advantages {
    position: relative;

    max-height: 800px;
    height: 100vh;

    &__wrap {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;

        width: 100%;
        height: 100%;
    }

    &__item {
        position: absolute;
        display: flex;
        flex-direction: column;
        align-items: center;

        

        &_center {
            top: 55.5%;
            left: 50%;
            transform: translate(-50%, - 50%);
            
        }

        &_budget {
            top: 40.5%;
            left: 0;
            transform: translate(-1.5%, -50%);
        }

        &_deadline {
            top: 76%;
            left: 0;
            transform: translate(-1.5%, -50%);
        }

        &_foreman {
            top: 35.8%;
            right: 0;
            transform: translate(4%, -50%);
        }

        &_masters {
            top: 74.5%;
            right: 0;
            transform: translate(2%, -50%);
        }
    }

    &__img {


        &_center {
        width: 34vw;
        }

        &_budget {
            width: 7vw;
        }
    }

    &__descrip {
        position: absolute;
        top: 79.5%;
        left: 22%;
    }

    &__title {
        font-size: 20 / 18 + em;
        font-weight: 300;
        text-transform: uppercase;
        padding-top: 1.1em;

        &_foreman {
            padding-top: 0.7em;
        }

        &_masters {
            padding-top: 1.5em;
        }
    }

    &__text {
        margin: 0;
        width: 16.8vw;
        text-align: center;
        font-weight: 300;
        padding-top: 0.7em;


        &_center {
            width: 16.5vw;
            text-align: left;
            font-size: 20 / 18 + em;
            padding-top: 0;
        }

        &_foreman {
            padding-top: 0.4em;
            width: 18.8vw;
        }
        &_masters {
            width: 19.5vw;
        }
    }
}