@import "../../globalSASS/global.scss";
.card {
    width: 21.2vw;

    background-color: #fff;
    box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    overflow: hidden;

    &:nth-child(odd) {
        margin: 0 1.5vw;
    }

    &__preview {
        position: relative;
        width: 100%;
    }

    &__img {
        width: 100%;
        border-radius: 10px;
    }

    &__text {
        position: absolute;
        display: flex;
        flex-direction: column;
        bottom: 7%;
        left: 5%;

        font-size: 20 / 18 + em;
        font-weight: 500;
        color: #fff;
    }

    &__title {
        text-transform: uppercase;
    }

    &__wrap {
        position: relative;
        margin-top: 1.9em;
        margin-left: 1.4em;
        margin-right: 1.4em;
    }

    &__description {
        display: flex;
        flex-direction: column;
        margin-top: 1.2em;
    }

    
    &__punct {
        margin: 0;
        font-weight: lighter;

        &_subtext {
            line-height: 0;
            font-size: 14 / 18 + em;
        }

        &_small {
            line-height: 1;
        }
    }
}