@import "../../globalSASS/global.scss";
.calculate {
	position: absolute;
	top: 51.5%;
	left: 50%;

	transform: translate(-50%, -50%);

	width: 62vw;
	height: 27vw;

	box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    background-color: #fff;

	&__wrap {
		width: 100%;
		height: 100%;
        position: relative;
    }
    
    &__img {
        margin-top: 5%;
        width: 110%;
    }

	&__card {
		position: absolute;
		top: 48.5%;
        left: 17%;
        
        transform: translate(-50%, -50%);

        display: flex;
        flex-direction: column;
        align-items: center;

        width: 29%;
        height: 32.5vw;

        box-shadow: 0px 0px 30px rgba(0, 0, 0, 0.25);
        border-radius: 10px;
        background-color: #F7F7F7;
    }
    
    &__subtitle {
        font-size: 27 / 18 + em;
        font-weight: 700;
        text-align: center;
        width: 8em;
        padding-top: 22.8%;
    }

    &__text {
        font-size: 20 / 18 + em;
        font-weight: 300;
        text-align: center;
        padding-top: 5%;
        width: 15em;
    }

    &__side-r {
        position: absolute;
        top: 0;
        right: 0;
        height: 100%;
        width: 67%;
    }

    &__form {
        width: 80%;

        margin: 4.5% 4.8%;

    }

    &__title {
        font-size: 34 / 18 + em;
        font-weight: 700;
    }

    &__choice {
        display: flex;
        padding-top: 2em;

        @media screen and (max-width: 1650px) {
            padding-top: 1.5em;
        }
    }

    &__inputs-l {
        display: flex;
        flex-direction: column;
    }

    &__type {
        display: flex;
        flex-direction: column;

        &_down {
            margin-top: 1.6em;

            @media screen and (max-width: 1650px) {
                margin-top: 1.2em;
            }
        }
    }

    &__name {
        font-size: 25 / 18 + em;
        font-weight: 300;
        padding-bottom: 1em;

        @media screen and (max-width: 1650px) {
            padding-bottom: 0.5em;
        }

        &_area {
            padding-bottom: 0.8em;

            @media screen and (max-width: 1650px) {
                padding-bottom: 0.5em;;
            }
        }
    }
    &__arrow {
        position: absolute;
        top: 50%;
        right: 0;
        width: 1.5em;
        height: 1em;

        transform: translate(-50%, -50%);
        z-index: 1;
    }

    &__inputs-r {
        margin-left: 9%;
    }

    @media screen and (max-width: 1850px) {
        font-size: 17px;
    }

    @media screen and (max-width: 1750px) {
        font-size: 16px;
    }
    

    @media screen and (max-width: 1550px) {
        font-size: 14px;
    }

    @media screen and (max-width: 1250px) {
        font-size: 13px;
    }

    @media screen and (max-width: 1150px) {
        font-size: 12px;
    }
}
